import React, { Component } from "react";

import Layout from "../components/Layout/Layout";
import LoginScreen from "../components/LoginScreen/LoginScreen";
import RoomScreen from "../components/RoomScreen/RoomScreen";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loggedIn: false,
      username: ""
    };
  }

  onLogin(e) {
    e.preventDefault();
    const username = document.querySelector("#l").value;
    document.querySelector("#l").value = "";
    this.setState({ ...this.state, loggedIn: true, username });
  }

  render() {
    return (
      <Layout>
        {!this.state.loggedIn ? <LoginScreen onLogin={() => this.onLogin} /> : null}
        {!this.state.username === "" ? (
          <RoomScreen username={this.state.username} />
        ) : null}
        <p className="z-3">Hello from the App!</p>
      </Layout>
    );
  }
}
export default App;
